// SASS variables
// Colors

$primary-color: #fafaf9;
$secondary-color: #be6741;
$complementary-color: #4198be;
$primary-100: #f5f5f4;
$primary-200: #e7e5e4;
$primary-300: #d6d3d1;
$primary-400: #a8a29e;
$primary-500: #78716c;
$primary-600: #57534e;
$primary-700: #44403c;
$primary-800: #292524;
$primary-900: #1c1917;

// Opacity
$primary-600-opacity: #57534e75;
$primary-700-opacity: #44403c75;
//Transitions & Shadows
$transition-1: all 400ms ease-in-out;
$box-shadow-small: 1px 4px 15px rgba(0, 0, 0, 0.32);
$box-shadow-large: 10px 10px 19px 5px rgba(0, 0, 0, 0.75);
$base-gradient: linear-gradient(135deg, #0000008a, #0000008a);
$border-small: 20px;

.light-mode {
  $primary-color: #fafaf9;
  $secondary-color: #be6741;
  $complementary-color: #4198be;
  $primary-100: #f5f5f4;
  $primary-200: #e7e5e4;
  $primary-300: #d6d3d1;
  $primary-400: #a8a29e;
  $primary-500: #78716c;
  $primary-600: #57534e;
  $primary-700: #44403c;
  $primary-800: #292524;
  $primary-900: #1c1917;
}
